import React from 'react';
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import * as colors from "@material-ui/core/colors";
import CssBaseline from "@material-ui/core/CssBaseline";
import CircularProgress from '@material-ui/core/CircularProgress';
import 'fontsource-roboto';
import GlobalMenu from './components/GlobalMenu';
import MainMenuPage from './pages/MainMenuPage';
import WordPage from './pages/WordPage';
import WordTransPage from './pages/WordTransPage';
import WordGrammerPage from './pages/WordGrammerPage';
import GrammerPage from './pages/GrammerPage';
import GrammerWritingPage from './pages/GrammerWritingPage';
import GrammerWritingTestPage from './pages/GrammerWritingTestPage';
import EnglishData from './context/EnglishData';
import GrammerData from './context/GrammerData';

interface AppProps {
}

interface AppState {
  currentYear: string,
  currentPage: pageNames,
  isLoaded: number,
}

class App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);
    let sPage:pageNames = 'WordPage';
    switch (localStorage.getItem('currentPage')) {
      case 'WordPage':  sPage = 'WordPage';
            break;
      case 'GrammerPage': sPage = 'GrammerPage';
            break;
      case 'WordGrammerPage': sPage = 'WordGrammerPage';
            break;
      case 'WordTransPage': sPage = 'WordTransPage';
            break;
      case 'GrammerWritingPage': sPage = 'GrammerWritingPage';
            break;
      case 'GrammerWritingTestPage': sPage = 'GrammerWritingTestPage';
            break;
      default: sPage = 'WordPage';
    }
    this.state = {
      currentYear: '',
      currentPage: sPage,
      isLoaded: 0,
    }
  }

  async componentDidMount() {
    await EnglishData.setWordData();
    await GrammerData.setGrammerData();
    await this.setState({isLoaded: 1});
  }

  render () {
    const theme = createMuiTheme({
      palette: {
        primary: {
          main: colors.blue[800],
        },
        type: 'dark',
      },
    });

    const changePage = async (val: pageNames) => {
      this.setState({
        currentPage: val
      })
      if (val === 'WordPage') {
        await EnglishData.setWordData(this.state.currentYear);
      } else if (val === 'WordTransPage') {
        await GrammerData.setGrammerData(this.state.currentYear);
      }
    }

    const changeCureentYear = async (year: string) => {
      await EnglishData.setWordData(year);
      await GrammerData.setGrammerData(year);
      await this.setState({
        currentYear: year,
        isLoaded: 2
      })
    }

    return (
      <ThemeProvider theme={theme}>
        <CssBaseline />
        {
          this.state.isLoaded === 0
          &&
          <>
            <div style={
              {
                height: '100vh',
                width: '100vw',
                display: 'table-cell',
                verticalAlign: 'middle',
                textAlign: 'center'
              }
            }>
              <CircularProgress />
              <p>now loading...</p>
            </div>
            </>
          }
        {
          this.state.isLoaded === 1
          &&
            <MainMenuPage 
              onChange={changeCureentYear}
            />
          }
        {
          this.state.isLoaded === 2
          &&
          <>
          {
            this.state.currentPage === 'WordPage'
            &&
            <WordPage />
          }
          {
            this.state.currentPage === 'WordTransPage'
            &&
            <WordTransPage />
          }
          {
            this.state.currentPage === 'WordGrammerPage'
            &&
            <WordGrammerPage />
          }
          {
            this.state.currentPage === 'GrammerPage'
            &&
            <GrammerPage />
          }
          {
            this.state.currentPage === 'GrammerWritingPage'
            &&
            <GrammerWritingPage />
          }
          {
            this.state.currentPage === 'GrammerWritingTestPage'
            &&
            <GrammerWritingTestPage />
          }

          <GlobalMenu
              currentPage={this.state.currentPage}
              changeCurrentPage= {(val) => changePage(val)}
          />
          </>
        }
      </ThemeProvider>
    );
  }
}

export default App;