import React, { useState, useEffect } from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import { makeStyles } from "@material-ui/core/styles";
import Paginate from '../components/Pagination';
import Grammer from '../components/GrammerWritingTestPage/Grammer';
import SideMenu from '../components/SideMenu';
import GrammerData from '../context/GrammerData';
import EnglishVoice from '../lib/EnglishVoice';
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked';
import ClearIcon from '@material-ui/icons/Clear';
import arrayShuffle from 'array-shuffle';

interface answerType {
  part: string,
  id: string,
  sentence: string,
  inputString: string,
  isCorrect: boolean,
}

const useStyles = makeStyles((theme) => ({
  result: {
    paddingBottom: '1rem',
    borderBottom: '1px solid #aaa'
  }
}));

const GrammerWritingTestPage:React.FC = () => {
  const timerLimit = 30;
  const firstPart = localStorage.getItem('grammerWritingCurrentPart') ?? GrammerData.getFirstPart();
  const wIndex: number = localStorage.getItem('grammerWritingWordIndex') ? Number(localStorage.getItem('grammerWritingWordIndex')) : 0;
  const [slides, setSlides] = useState(arrayShuffle(GrammerData.getSentences().filter(w => w.part === firstPart)));
  const [wordIndex, setWordIndex] = useState<number>(wIndex);
  const [currentPart, setCurrentPart] = useState<string>('');
  const [isAnsComplated, setIsAnsComplated] = useState<boolean>(false);
  const [inputString, setInputString] = useState<string>('');
  const [speechSpeedRate, setSpeechSpeedRate] = useState<number>(0.8);
  const [timer, setTimer] = useState<number>(0);
  const [answer, setAnswer] = useState<answerType[]>([]);
  const [isEndAnswer, setIsEndAnswer] = useState<boolean>(false);

  const slidePrev = () => {
    //テストのため戻るボタンは無効に
  }

  const slideNext = (ans:answerType[] | null = null) => {
    if (ans !== null) {
      const answerWk:answerType[] = ans!.map(a => {
        if (a.part === currentPart && a.id === slides[wordIndex].id) {
          return ({
            part: a.part,
            id: a.id,
            sentence: a.sentence,
            inputString: inputString,
            isCorrect: a.isCorrect,
          });
        }
        return a;
      })
      setAnswer(answerWk);
      sessionStorage.setItem('grammerWritingAnswer', JSON.stringify(answerWk));
    }

    if (wordIndex < slides.length-1) {
      const i:number = wordIndex + 1;
      setWordIndex(i);
      localStorage.setItem('grammerWritingTestWordIndex', i.toString());
      setIsAnsComplated(false);
      setInputString('');
      setTimer(0);
    } else {
      setIsEndAnswer(true);
    }
  }


  const changeCurrentPart = async (val: string) => {
    if (currentPart !== val) {
      localStorage.setItem('grammerWritingCurrentPart', val);
      setWordIndex(0);
      localStorage.setItem('grammerWritingWordIndex', '0');
      setIsAnsComplated(false);
      setInputString('');
      setTimer(0);
      setAnswer([]);
      setIsEndAnswer(false);
      let s:any = [];
      if (val === 'favorite') {
        s = await GrammerData.getFavoriteSentenceData();
      } else {
        s = await GrammerData.getSentences().filter(w => w.part === val);
      }
      setSlides(arrayShuffle(s));
      setCurrentPart(val);
      sessionStorage.setItem('grammerWritingAnswer', '');
    }
  }

  const changeSpeechSpeedRate = (val: number) => {
    setSpeechSpeedRate(val);
  }

  const changeInputString = (val: string) => {
    setInputString(val);
  }

  const changeIsAnsComplated = (val: boolean, is: string) => {
    let ans:answerType[] = answer;
    if (answer.filter(a => (a.part === currentPart && a.id === slides[wordIndex].id)).length >= 1) {
      ans = ans.map(a => {
        if (a.part === currentPart && a.id === slides[wordIndex].id) {
          return ({
            part: a.part,
            id: a.id,
            sentence: a.sentence,
            inputString: is,
            isCorrect: val
          });
        }
        return a;
      })
    } else {
      ans.push({
        part: currentPart,
        id: slides[wordIndex].id,
        sentence: slides[wordIndex].sentence.replaceAll('  ', ' '),
        inputString: '',
        isCorrect: val,
      });
    }
    setAnswer(ans);
    sessionStorage.setItem('grammerWritingAnswer', JSON.stringify(ans));

    setIsAnsComplated(val);
    if (val) {
      EnglishVoice.speech('OK. '+slides[wordIndex].sentence, 'en-US', 1);
      slideNext(ans);
    }
  }

  useEffect(() => {
    const setPart = async () => {
      let part:string = '';
      part = await localStorage.getItem('grammerWritingCurrentPart') ?? GrammerData.getFirstPart();
      await changeCurrentPart(part);
    };
    setPart();
    const id = setInterval(() => {
      setTimer(t => t + 1);
    }, 1000);
    return () => clearInterval(id);
  }, []);

  useEffect(() => {
    let ans:answerType[] = answer;
    if (answer.filter(a => (a.part === currentPart && a.id === slides[wordIndex].id)).length == 0) {
      ans.push({
        part: currentPart,
        id: slides[wordIndex].id,
        sentence: slides[wordIndex].sentence.replaceAll('  ', ' '),
        inputString: '',
        isCorrect: false,
      });
      setAnswer(ans);
      sessionStorage.setItem('grammerWritingAnswer', JSON.stringify(ans));
    }

    if (timer == timerLimit && !isEndAnswer) {
      if (wordIndex < slides.length-1) {
        EnglishVoice.speech('next', 'en-US', 1);
      } else {
        setIsEndAnswer(true);
        EnglishVoice.speech('end', 'en-US', 1);
      }
    }
    if (timer > (timerLimit+2) && !isEndAnswer) {
      slideNext(ans);
    }
  });

  const classes = useStyles();
  return (
    <>
      {
        currentPart !== ''
        &&
        <SideMenu
          categories={GrammerData.getCategories()}
          parts={GrammerData.getParts()}
          currentPart={currentPart}
          changeCurrentPart={(val) => changeCurrentPart(val)}
          speechSpeedRate={speechSpeedRate}
          changeSpeechSpeedRate={(val) => changeSpeechSpeedRate(val)}
        />
      }
      <Container>
        {
          !isEndAnswer
          &&
          <>
            <Paginate
              index={wordIndex}
              maxPage={slides.length}
              isViewPrev={false}
              isViewNext={true}
                  onClickPrev={(slidePrev)}
              onClickNext={() => slideNext()}
            />
            <hr/>
            {
              slides.length == 0
              &&
              <p>no data</p>
            }
            {
              slides.length > 0
              &&
              <Grammer
                id={slides[wordIndex].id}
                currentPart={currentPart}
                sentence={slides[wordIndex].sentence.replaceAll('  ', ' ')}
                inputString={inputString}
                translate={slides[wordIndex].sentenceTrans}
                speechSpeedRate={speechSpeedRate}
                isAnsComplated={isAnsComplated}
                changeInputString={(v) => changeInputString(v)}
                changeIsAnsComplated={(v:boolean, s:string) => changeIsAnsComplated(v, s)}
              />
            }
            {
              timer >= timerLimit
              &&
              <p>time over</p>
            }
            {
              timer < timerLimit
              &&
              <p>Time: {timerLimit-timer}</p>
            }
          </>
        }
        {
          isEndAnswer
          &&
          <>
            <h3>result</h3>
            <div style={{height:'80h', overflowY:'auto'}}>
              {
                answer.map(a => 
                  <Grid container className={classes.result}>
                    <Grid item xs={2}>
                    <Typography variant={'body1'}>
                      {
                        a.isCorrect ?
                        <RadioButtonUncheckedIcon color={'primary'}/>
                        :
                        <ClearIcon color={'error'}/>
                      } 
                    </Typography>
                    </Grid>
                    <Grid item xs={10}>
                      <Typography variant={'body1'}>{a.sentence}</Typography>
                      <Typography color={'error'}>{!a.isCorrect ? a.inputString+'': ''}&nbsp;</Typography>
                    </Grid>
                  </Grid>
                )
              }
            </div>
          </>
        }
      </Container>
    </>
  );
}

export default GrammerWritingTestPage;